import {ESrcField, JSrcFields, JSrcFieldsTree, JSrcIdent, JSubItem, SRC, srcRef, srcUri} from "lib/wsp/src";
import {IWspUiEnv, JWspUriChangeMsg, WSP, Wsp} from "lib/wsp/wsp";
import {EWspChangesEvts, WspsLivePlace} from "lib/wsp/wspsLive";
import {InfoCurrentItem, InfoHighlighItemSgn, InfoReqCurrentItem, IShortDescCtx, ITEM, JItemNetLnkResp, JItemNetResp, srcRefSub} from "lib/wsp/item";
import {SrcBtn, SRCDRAWER, SrcDrawer, SrcPointer, SrcPointerDblClick, SrcPointerFastSelect, SrcPointerSetterAction} from "back/wsp/widgets/srcDrawer";
import {ActionBtn, Button, ButtonToggle, OButtonToggleInit} from "back/commons/widgets/buttons";
import {AccelKeyMgr, ACTION, Action, ActionSeparator, EButtonUiContext, IAccelKeyMgrPointer, IAction} from "lib/commons/actions";
import {POPUP} from "back/commons/widgets/popups";
import {OpenFastFindItem} from "back/wsp/actions/srcActions";
