customElements.define("wsp-net-items", NetItems);

class GridDataRowNetItems extends GridDataRowJsonTree<INetNode> {
	getData(key: ESrcField): any {
		const src = this.rowDatas.src;
		return src ? src[key] : undefined;
	}
}

class NetItemsDataHolder extends GridDataHolderJsonTree<INetNode> {

	fieldsLnks: ESrcField[];
	fieldsFrom?: ESrcField[];

	/** Noeud racine des this._datas. */
	root: INetNode;

	/** Pour refresh, set des items listés (pas des subItems), en source et ou cible des liens. */
	protected _srcUsed: Set<srcRef> = new Set();

	/** Cache des itemNets recherchés */

	//protected _itemNetFetched: Map<srcRefSub, JItemNetResp> = new Map();

	/**
	 *
	 * @param netState todo abstraire via une interface INetState
	 */
	constructor(public netState: NetItems) {
		super('ch');
		this.setDefaultOpenState((node: INetNode) => (node as ISubNetNode).subItem != null);
	}

	protected newDataRow(): GridDataRowJsonTree<INetNode> {return new GridDataRowNetItems(this, null)}

	async openFolderAsync(rowKey: INetNode): Promise<boolean> {
		if (this.ch(rowKey) === null) {
			const srcRef = ITEM.srcRefSub(rowKey.src);
			//let itemNet = this._itemNetFetched.get(srcRef);
			this.setFolderSt(rowKey, EFolderState.opening);
			//if (!itemNet) {
			const wsp = this.netState.wsp;
			const conf = this.netState.getNetConf();
			const itemNet = await ITEM.fetchSrcNetJson(wsp, this.netState, srcRef, this.fieldsLnks,
				this.netState.getDir(), true, conf.linksAllowed, conf.itemTargeted, 100, 1, this.fieldsFrom
			);
			if (!this.isRowkeyAlive(rowKey)) return false; //racecond
			//this._itemNetFetched.set(srcRef, itemNet);
			//}
			const newNode: INetNode = {src: itemNet.src, ch: null};
			this.fillNodesFromItemNetResp(itemNet, newNode);
			this.setFolderSt(rowKey, EFolderState.closed);
			if (this.replaceRowKey(rowKey, newNode, false)) return super.openFolder(newNode);
			return false;
		}
		return this.openFolder(rowKey);
	}

	/** Appelé à l'init, à chaque chgt de config, en cas de perte de connexion. */
	async rebuildFromRoot(clearCaches?: boolean, preserveOpenStates?: boolean) {
		if (clearCaches) this.clearCaches();
		let oldTree = preserveOpenStates ? this.root : null;
		this.setDatas([]); //todo ifEmpty du grid
		const srcRef = this.netState.getRootSrcRef();
		if (!srcRef) return;
		//let itemNet = this._itemNetFetched.get(srcRef);
		//if (!itemNet) {
		const wsp = this.netState.wsp;
		const conf = this.netState.getNetConf();
		this.fieldsLnks = wsp.getShortDescFields();
		const dir = this.netState.getDir();
		this.fieldsFrom = dir === "desc" && (conf.fromSubItems === "flat" || conf.fromSubItems === "unfolded") ? this.fieldsLnks.concat("itSubItems") : undefined;
		const itemNet = await ITEM.fetchSrcNetJson(wsp, this.netState, srcRef, this.fieldsLnks,
			dir, true, conf.linksAllowed, conf.itemTargeted, 100, 1, this.fieldsFrom
		);
		if (srcRef !== this.netState.getRootSrcRef()) return; //raceCond
		//this._itemNetFetched.set(srcRef, itemNet);
		//}
		this.root = {src: itemNet.src, ch: null};
		this._srcUsed.add(SRC.srcRef(itemNet.src));
		this.fillNodesFromItemNetResp(itemNet, this.root);
		this.setDatas(this.root.ch || []);
		if (oldTree) {
			//On réouvre les noeuds
			const openNodes = async (oldNode: INetNode, newNode: INetNode) => {
				if (newNode.ch == null) return; // pas de fils pour newNode
				for (let oldN of oldNode.ch) {
					if (!oldN.src || this.folderSt(oldN) !== EFolderState.opened) continue;
					//oldN ouvert, on cherche newN
					const oldRef = SRC.srcRef(oldN.src);
					for (let newN of newNode.ch) {
						if (SRC.srcRef(newN.src) === oldRef) {
							await this.openFolderAsync(newN)
							newN = newNode.ch.find((n) => n.src && SRC.srcRef(n.src) === oldRef);
							await openNodes(oldN, newN);
							break;
						}
					}
				}
			}
			await openNodes(oldTree, this.root);
		}
	}

	refreshSrc(ident: JSrcIdent, stOnly?: boolean) {
		if (!this.root) return;
		const srcRef = SRC.srcRef(ident);
		if (!this._srcUsed.has(srcRef)) return; //item pas dans le réseau affiché
		const scanNode = (node: INetNode) => {
			if (node.src && SRC.srcRef(node.src) === srcRef) {
				if (node.ch === null) {
					//Fils non calculés, on ne refresh que le node.
					this.refreshSrcNode(node);
				} else if (this.netState.getDir() === "asc" || stOnly) {
					//Fils calculés, mais liens ascendants ou chgt de st uniquement
					// on ne refresh que le node.
					this.refreshSrcNode(node);
					// on poursuit le scan
					for (const ch of node.ch) scanNode(ch);
				} else {
					//Fils calculés et liens descendants
					if (node === this.root) {
						this.rebuildFromRoot(true, true);
					} else if (this.folderSt(node) === EFolderState.opened) {
						//on refresh l'itemNet complet.
						this.closeFolder(node);
						this.openFolderAsync(node);
					} else {
						//dossier clos, on purge le calcul des fils
						this.setCh(node, null); // à recalculer.
						this.setFolderSt(node, EFolderState.closed);
						this.refreshSrcNode(node);
					}
					return;
				}
			}
			if (node.ch) for (const ch of node.ch) scanNode(ch);
		}
		scanNode(this.root);
	}

	protected async refreshSrcNode(node: INetNode) {
		const fields = await WSP.fetchSrc(this.netState.wsp, this.netState, ITEM.srcRefSub(node.src), this.fieldsFrom || this.fieldsLnks);
		if (!this.isRowkeyAlive(node)) return; //raceCond
		node.src = fields;
		this.resetRowCache(node);
		this.grid?.invalidateRows(this.getOffset(node));
	}

	protected fillNodesFromItemNetResp(resp: JItemNetResp, toFill: INetNode) {
		//toFill.src = resp.src; //déjà renseigné
		toFill.net = resp;
		switch (this.netState.getDir() !== "desc" ? "folded" : this.netState.getNetConf().fromSubItems || "folded") {
		case "folded":
			//Pas de subItems affihcés en fils.
			toFill.lnks = resp.lnks.length > 0 ? resp.lnks : undefined;
			break;
		case "flat":
			for (const lnk of resp.lnks) {
				const subItem = ITEM.findSubItem(lnk.subIt, resp.src.itSubItems);
				if (!subItem) {
					if (!toFill.lnks) toFill.lnks = [];
					toFill.lnks.push(lnk);
				} else {
					if (!toFill.subNodes) toFill.subNodes = new Map<string, ISubNetNode>();
					let subNetNode: ISubNetNode = toFill.subNodes.get(lnk.subIt);
					if (!subNetNode) {
						subNetNode = {
							src: resp.src,
							subItem: subItem,
							lnks: [],
							ch: null //à calculer
						};
						toFill.subNodes.set(lnk.subIt, subNetNode);
					}
					subNetNode.lnks.push(lnk);
				}
			}
			break;
		case "unfolded":
			for (const lnk of resp.lnks) {
				const subItems = ITEM.findSubItemStack(lnk.subIt, resp.src.itSubItems);
				if (!subItems) {
					if (!toFill.lnks) toFill.lnks = [];
					toFill.lnks.push(lnk);
				} else {
					let curr = toFill;
					for (let i = 0; i < subItems.length; i++) {
						const subIt = subItems[i];
						if (!curr.subNodes) curr.subNodes = new Map<string, ISubNetNode>();
						let subNetNode: ISubNetNode = curr.subNodes.get(subIt.id);
						if (!subNetNode) {
							subNetNode = {
								src: resp.src,
								subItem: subIt,
								ch: null //à calculer
							};
							curr.subNodes.set(subIt.id, subNetNode);
						}
						if (i == subItems.length - 1) (subNetNode.lnks || (subNetNode.lnks = [])).push(lnk);
						curr = subNetNode;
					}
				}
			}
			break;
		}

		const buildCh = (node: INetNode) => {
			//Construction des nodes fils
			node.ch = [];
			// 1. SubItems
			if (node.subNodes) for (const subN of node.subNodes.values()) {
				node.ch.push(subN);
				buildCh(subN);
			}
			// 2. Links
			if (node.lnks) for (const lnk of node.lnks) {
				node.ch.push({
					src: lnk.src, //cible du lien
					ch: null //à calculer
				});
				this._srcUsed.add(SRC.srcRef(lnk.src));
			}
		}

		buildCh(toFill);
		if (toFill.ch.length === 0) {
			toFill.ch.push({src: null, ch: undefined});
		}
	}

	clearCaches() {
		this._srcUsed.clear();
		//this._itemNetFetched.clear();
	}
}

/** Noeud représentant un item ou un subItem racine cible d'un lien. */
interface INetNode {

	/**
	 * Src du noeud pour l'affichage des rows lorsqu'ils sont une racine d'une cible d'un lien.
	 * Cet src est soit issu de JItemNetLnkResp.src (cible du lien amont), soit d'un refresh lié à un update de l'item container.
	 *
	 * Si null, indique une absence de noeuds fils dans le réseau.
	 */
	src: JSrcFields | null

	/**
	 * Liens sortants (disponible en async, une fois chargés).
	 */
	net?: JItemNetResp

	/**
	 * En mode fromSubItems='folded', égal à this.net.lnks
	 * En mode fromSubItems='unfolded'|'flat',
	 * 	- Si ce noeud est une racine (pas un ISubNetNode) égal aux net.lnks qui ne partent pas d'un subItem (JItemNetLnkResp.subIt==null).
	 * 	- Si ce noeud est un ISubNetNode égal aux net.lnks qui partent de ce subItem (JItemNetLnkResp.subIt==this.subNode.id).
	 */
	lnks?: JItemNetLnkResp[]

	/**
	 * Map intermédiaire pour construire les noeuds fils this.ch
	 * En mode fromSubItems='folded', reste indéfini
	 * En mode fromSubItems='unfolded'|'flat' liste les subItems fils contenant des liens.
	 */
	subNodes?: Map<string, ISubNetNode>

	/**
	 * Fils issus de la concaténation des noeuds construits à partir de this.subNodes (ISubNetNode) et this.lnks.
	 * null = non calculé (this.net non fetché)
	 * undefined = pas de fils
	 */
	ch: undefined | null | INetNode[]
}

/**
 * Pour le mode fromSubItems='unfolded' | 'flat'.
 * Noeud représentant un subItem interne à INetNode.
 */
interface ISubNetNode extends INetNode {
	/**
	 * L'affichage doit se limiter aux infos dans ce ISubNetNode.subNode, les infos de son INetNode.src contenant
	 * sont déjà affichées dans les rows ancêtres.
	 */
	subItem: JSubItem
}

class CellBuilderNet extends CellBuilderSrcIconCode {
	protected netItems: NetItems;

	setNetItems(netItems: NetItems) {
		this.netItems = netItems;
		return this;
	}

	redrawCell(row: GridDataRowJson<any | INetNode>, root: HTMLElement): void {
		(root as JSrcFieldsPointer)._srcFields = (row.rowDatas as INetNode).src;
		let span = root.firstElementChild as HTMLElement;
		if (!span) span = this._buildContent(row, root);
		this.redrawIcon(row, span);
		//Code
		span = span.nextElementSibling as HTMLElement;
		DOM.setTextContent(span, this._getValue(row));
		//Space
		span = span.nextElementSibling as HTMLElement;
		DOM.setTextContent(span, this._getSpace(row));

		DOM.setAttr(root, "title", this._getDescription(row) || undefined);

		//On masque l'icone si row de type subItem
		const isSubItem = (row.rowDatas as ISubNetNode).subItem != null;
		DOM.setHidden(root.firstElementChild, isSubItem); //pas d'icone si subItem
		(root.previousElementSibling as HTMLElement).classList.toggle("subItem", isSubItem); //twisty != si subItem
	}

	protected _buildContent(row: IGridDataRow, root: HTMLElement): HTMLElement {
		//icon
		const iconSpan = super._buildContent(row, root);
		//code
		root.appendChild(document.createElement('span'));
		//space
		const space = root.appendChild(document.createElement('span'));
		space.classList.add("space");
		return iconSpan;
	}

	protected _getValue(row: GridDataRowJson<any | INetNode>): string {
		const node = row.rowDatas as INetNode;
		if ((node as ISubNetNode).subItem) return (node as ISubNetNode).subItem.ti;
		if (node.src === null) return this.netItems.getDir() === "asc" ? "Aucun item référençant" : "Aucun item référencé";
		return row.cacheHolder['code'] || (row.cacheHolder['code'] = this.srcRdr.getMainName(node.src, this.wspMetaUi));
	}

	protected _getIcon(row: GridDataRowJson<any | INetNode>): string | null {
		const node = row.rowDatas as INetNode;
		if ((node as ISubNetNode).subItem || node.src === null) return null;
		return row.cacheHolder['icon'] || (row.cacheHolder['icon'] = this.srcRdr.getIcon(node.src, this.wspMetaUi));
	}

	protected _getSpace(row: GridDataRowJson<any | INetNode>): string | null {
		const node = row.rowDatas as INetNode;
		if ((node as ISubNetNode).subItem || node.src === null) return null;
		const sp = ITEM.extractSpaceLabel(node.src.srcUri, null);
		return row.cacheHolder['space'] || (row.cacheHolder['space'] = sp == null ? null : `(${sp})`);
	}
}
